'use client';

import React from "react";
import { useTheme } from './context/ThemeContext';
import { createStyles } from './utils/styles';
import { GlassCard } from './shared/SharedComponents';

interface AnalysisResult {
  title: string;
  confidence: number;
  factors: string[];
  latency: number;
  timestamp: string;
  version: string;
}

interface AnalysisResultsProps {
  result: AnalysisResult | null;
}

// ============================================================================
// ANALYSIS RESULTS COMPONENT
// ============================================================================
const AnalysisResults = ({ result }: AnalysisResultsProps) => {
  const { isDark } = useTheme();
  const styles = createStyles(isDark);

  // Handle empty/undefined data gracefully
  if (!result) return null;

  const {
    title = 'Analysis Results',
    confidence = 0,
    factors = [],
    latency = 0,
    timestamp = new Date().toLocaleString(),
    version = '1.0.0'
  } = result;

  return (
    <GlassCard style={{ marginBottom: '2rem' }} glowColor={isDark ? '#10b981' : '#059669'}>
      <h3 
        className="m-0 mb-6 text-2xl font-bold flex items-center gap-3"
        style={{ 
          fontFamily: "'Inter', -apple-system, sans-serif",
          ...styles.gradientText,
          textShadow: isDark ? '0 2px 4px rgba(0, 0, 0, 0.5)' : 'none'
        }}
      >
        ‚ÑπÔ∏è Analysis Details
      </h3>

      <div className="flex justify-between items-start mb-6 flex-wrap gap-5">
        <h4 
          className="m-0 text-xl font-semibold flex-1 leading-snug"
          style={{ 
            fontFamily: "'Inter', -apple-system, sans-serif",
            color: isDark ? '#f1f5f9' : '#1e293b'
          }}
        >
          {title}
        </h4>
        
        <div 
          className="px-5 py-3 rounded-3xl text-sm font-bold backdrop-blur-xl border border-white/20"
          style={{
            background: isDark 
              ? 'linear-gradient(135deg, #6366f1, #8b5cf6)' 
              : 'linear-gradient(135deg, #3b82f6, #6366f1)',
            color: 'white',
            fontFamily: "'Inter', -apple-system, sans-serif",
            boxShadow: isDark 
              ? '0 8px 24px rgba(139, 92, 246, 0.4)' 
              : '0 8px 24px rgba(59, 130, 246, 0.3)'
          }}
        >
          {confidence}% Confidence
        </div>
      </div>

      {/* Key Factors Section - Safely handle empty factors array */}
      {factors && factors.length > 0 && (
        <div className="mb-6">
          <h5 
            className="text-lg font-semibold mb-4 flex items-center gap-2"
            style={{ 
              fontFamily: "'Inter', -apple-system, sans-serif",
              color: isDark ? '#f1f5f9' : '#1e293b'
            }}
          >
            üéØ Key Factors
          </h5>
          <div className="flex flex-wrap gap-3">
            {factors.map((factor, index) => {
              // Handle case where factor might be null/undefined
              if (!factor) return null;
              
              return (
                <span 
                  key={`factor-${index}`} 
                  className="px-4 py-2 rounded-3xl text-sm font-semibold border"
                  style={{
                    background: isDark 
                      ? 'linear-gradient(135deg, rgba(139, 92, 246, 0.2), rgba(168, 139, 250, 0.3))'
                      : 'linear-gradient(135deg, rgba(59, 130, 246, 0.15), rgba(99, 102, 241, 0.25))',
                    color: isDark ? '#c4b5fd' : '#4f46e5',
                    fontFamily: "'Inter', -apple-system, sans-serif",
                    borderColor: isDark 
                      ? 'rgba(139, 92, 246, 0.3)' 
                      : 'rgba(59, 130, 246, 0.3)',
                    backdropFilter: 'blur(8px)'
                  }}
                >
                  {factor}
                </span>
              );
            })}
          </div>
        </div>
      )}

      {/* Metadata Section */}
      <div 
        className="p-6 rounded-2xl border"
        style={{
          background: isDark 
            ? 'linear-gradient(135deg, rgba(15, 23, 42, 0.4), rgba(30, 41, 59, 0.4))'
            : 'linear-gradient(135deg, rgba(248, 250, 252, 0.6), rgba(255, 255, 255, 0.6))',
          borderColor: isDark 
            ? 'rgba(148, 163, 184, 0.2)' 
            : 'rgba(203, 213, 225, 0.3)'
        }}
      >
        <h6 
          className="text-base font-semibold mb-3 flex items-center gap-2"
          style={{ 
            fontFamily: "'Inter', -apple-system, sans-serif",
            color: isDark ? '#f1f5f9' : '#1e293b'
          }}
        >
          üìä Analysis Metadata
        </h6>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
          <div>
            <span 
              className="font-medium block"
              style={{ 
                color: isDark ? '#94a3b8' : '#64748b',
                fontFamily: "'Inter', -apple-system, sans-serif"
              }}
            >
              Response Time:
            </span>
            <span 
              className="font-bold"
              style={{ 
                color: isDark ? '#f1f5f9' : '#1e293b',
                fontFamily: "'Inter', -apple-system, sans-serif"
              }}
            >
              {latency}ms
            </span>
          </div>
          <div>
            <span 
              className="font-medium block"
              style={{ 
                color: isDark ? '#94a3b8' : '#64748b',
                fontFamily: "'Inter', -apple-system, sans-serif"
              }}
            >
              Generated:
            </span>
            <span 
              className="font-bold"
              style={{ 
                color: isDark ? '#f1f5f9' : '#1e293b',
                fontFamily: "'Inter', -apple-system, sans-serif"
              }}
            >
              {timestamp}
            </span>
          </div>
          <div>
            <span 
              className="font-medium block"
              style={{ 
                color: isDark ? '#94a3b8' : '#64748b',
                fontFamily: "'Inter', -apple-system, sans-serif"
              }}
            >
              Version:
            </span>
            <span 
              className="font-bold"
              style={{ 
                color: isDark ? '#f1f5f9' : '#1e293b',
                fontFamily: "'Inter', -apple-system, sans-serif"
              }}
            >
              {version}
            </span>
          </div>
        </div>
      </div>
    </GlassCard>
  );
};

export default AnalysisResults;
